/* styles-sm.css */
/* Add styles for small screens */
.sofa-icons-container {
    display: flex;
    justify-content: space-around;
    align-items: center; /* Align icons to the center vertically */
    flex-wrap: wrap; /* Allow icons to wrap to the next line on smaller screens */
}

.sofa-icon {
    margin: 0 5px; /* Adjust margin between icons as needed */
}
