body {
    margin: 0;
    padding: 0;
    font-family: Arial, sans-serif;
    background-color: #F8F8F8;
}

.navbar {
    background: #2883FF;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
}
.nav-cont{
    margin-left: 50px;
}
.navbar a {
    font-family: 'Arial';
    font-size: 30px;
    font-weight: 500;
    color: #ffffff;
    text-decoration: none;
    margin: 0 20px;
}

.navbar a img {
    width: 40px;
    height: 40px;
    padding: 5px;
}

.cslr {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
}

.text-section,
.image-section {
    flex: 1;
    padding: 20px;
    box-sizing: border-box;
}

.button {
    background: #2883FF;
    color: #ffffff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.card img {
    width: 149px;
    height: 149px;
}

.tv-section {
    margin: 50px 50px;
}

.tv-section button {
    margin: 0 5px;
    width: 25%;
    padding: 10px;
}
.tv-section-inner{
    background-color: #ffffff;
    border-radius: 20px;

}

.ftco-gallery {
    background-image: url('./images/subs.png');
    background-size: cover;
    background-position: center;
    padding: 20px; /* Add padding for spacing around the form */
}

/* Style the input type box */
.ftco-gallery .form-control {
    border-radius: 8px;
    height: 70px;
    width: 100%;
    font-size: 28px;
}

.ftco-footer {
    background-image: url('./images/footer.png');
    background-size: cover;
    background-position: center;
    color: #ffffff;
    padding: 50px 0;
    text-align: center;
}

@media (max-width: 767.98px) {
    .cslr {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar {
        flex-direction: column;
        align-items: flex-start;
        padding: 10px;
    }

    .navbar a {
        margin: 10px 0;
    }

    .text-section,
    .image-section {
        padding: 10px;
    }

    .tv-section button {
        width: 70%;
        margin: 10px 0;
    }
}


.arrow-circle {
    width: 40px;
    height: 40px;
    background-color: #2883FF;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 10px;
}

.arrow-circle span {
    color: white;
    font-size: 24px;
}



.carousel-caption {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.content-container {
    background: rgba(255, 255, 255, 0.8);
    padding: 20px;
    border-radius: 10px;
    position: relative;
    text-align: left;
}

.icons-container {
    position: absolute;
    bottom: 10px;
    right: 10px;
    display: flex;
}


/* CSS File */
.icon-square {
    width: 50px; /* Set your desired width */
    height: 50px; /* Set your desired height */
    background-color: #ffffff; /* Set your desired background color */
    border: 2px solid #000000; /* Set your desired border color and thickness */
    margin: 5px; /* Set margin as needed */
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 24px; /* Set your desired font size for the symbols */
}

/* Additional CSS for larger symbols */
.material-symbols-outlined {
    font-size: 30px; /* Set your desired font size for the symbols */
}




.email-input-container {
    position: relative;
    width: 70%; 
    margin: 0 auto; 
}

.email-input {
    width: 100%; 
}

.email-arrow-icon {
    position: absolute;
    right: 10px; 
    top: 50%; 
    transform: translateY(-50%);
    width: 30px;
    height: 30px;
}
@media (max-width: 767px) {
    .text-center.tv-section h1,
    .text-center.tv-section h2,
    .text-center.tv-section button {
        text-align: center; 
        margin: 0 auto 10px; 
    }

    .text-center.tv-section img {
        width: 100%; 
        margin: 0 auto 20px; 
        display: block; 
    }
}


/* styles.css */

.border-style {
    border: 2px solid transparent;
    border-radius: 5px;
    padding: 8px 15px;
    background: linear-gradient(to bottom, rgba(255, 255, 255, 0.7), rgba(255, 255, 255, 0.7));
    color: #fff;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
}

.text-style {
    font-size: 15px;

    font-family: Arial, sans-serif;
    font-weight: bold;
}




.custom-navbar {
    background-color: #2883FF;
    border: 1px solid #2883FF;
    height: 90px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 20px;
  }
  
  .custom-navbar .brand-logo {
    display: flex;
    align-items: center;
  }
  
  .custom-navbar .brand-logo img {
    margin-right: 10px;
  }
  
  .custom-navbar .brand-logo span {
    color: white;
    font-size: 18px;
  }
  
  .custom-navbar .nav-menu ul {
    list-style-type: none;
    display: flex;
    align-items: center;
    gap: 20px;
    margin: 0; 
    padding: 0; 
  }
  
  .custom-navbar .nav-menu .nav-item .nav-link {
    color: white;
    text-decoration: none;
    font-size: 16px;
  }

  
 
.circle1{
    width: 54px;
    height: 54px;
    border-radius: 50%;
    margin-right: 10px;
    border: 2px solid white; 
  }
  

